html,
body {
  height: 100%;
}
body {
  background: #eeff06;
  background: radial-gradient(ellipse at center, #b2cc1d 0%, #1f1d07 70%);
  background-size: 100%;
}
span {
  margin: 0;
  padding: 0;
}
.timer {
  font-family: ‘Share Tech Mono’, monospace;
  color: #ffffff;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #daf6ff;
  text-shadow: 0 0 20px rgb(192, 164, 86), 0 0 20px rgba(10, 175, 230, 0);
}

.field {
  display: inline-flex;
  align-items: center;
  margin-right: 20px;
}
.value {
  letter-spacing: 0.05em;
  font-size: 80px;
  padding: 5px 0;
}
